import { Component, HostBinding, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { fadeStateTrigger } from '../shared/animations/fade.animation';
import { trigger, state, transition, style, group, animate } from '@angular/animations';
import * as $ from 'jquery';
import { NavBarModel, navBar } from '../shared/models/nav-bar.model';

@Component({
  selector: 'wfm-auth',
  templateUrl: './auth.component.html',
  styleUrls: ['./auth.component.css'],
  animations: [fadeStateTrigger]
})
export class AuthComponent implements OnInit {
  @HostBinding('@fade') a = true;

  NavBarModel: NavBarModel[];

  constructor(private router: Router) { }

  ngOnInit() {
    this.NavBarModel = navBar;
  }
  toggleNavMobile() {
    $('#mobToggle').slideToggle(300);
  }
  navClose() {
    $('#mobToggle').slideUp(300);
  }
}
